import { swiper, swiperSlide } from 'vue-awesome-swiper';
import vPlayListCard from '../../../components/card/findCard/recommend/playListCard';
import vActivityCard from '../../../components/card/findCard/recommend/activityCard';
import vMvCard from '../../../components/card/findCard/recommend/mvCard';
const imgList = [
  '/static/banner1.jpg',
  '/static/banner2.jpg',
  '/static/banner3.jpg',
  '/static/banner4.jpg'
];
export default {
  name: 'v-recommend',
  props: {
    playlists: {
      type: Array
    },
    activitys: {
      type: Array
    },
    MVs: {
      type: Array
    }
  },
  components: {
    swiper,
    swiperSlide,
    vPlayListCard,
    vActivityCard,
    vMvCard
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination',
        paginationClickable: true,
        autoplay: 2500
      },
      slide_list: imgList
    };
  }
};



