
import api from '../../../api/index';
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
;
import vPlayList from '../../../components/card/findCard/playList/playList';
export default {
  name: 'v-play-lists',
  components: {
    vPlayList,
    ButtonTab,
    ButtonTabItem
  },
  data () {
    return {
      index: 0,
      keys: 'new',
      playlists: []
    };
  },
  mounted: function() {
    this.getTopPlaylistResource();
  },
  methods: {
    type () {
      this.getTopPlaylistResource();
    },
    getTopPlaylistResource() {
      this.keys = this.index ? 'hot' : 'new';
      this.playlists = [];
      this.$store.commit('update_loading', true);
      api.getTopPlaylistResource(this.keys, 20, 0).then((response) => {
        this.playlists = response.data.playlists;
        // $nextTick() dom 
        this.$nextTick(() => {
          this.$store.commit('update_loading', false);
        });
      })
        .catch((response) => {
          console.log(response);
        });
    }
  }
};



